import React, { useState } from 'react';
import { Collapse } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import {
  colorBackground,
  colorHover,
  colorText,
  colorBorder,
} from '@/utils/palette';
import { marginLeft, right, left } from '@/utils/directions';
import Cookies from 'universal-cookie';
import { TopbarBack, TopbarDownIcon } from './BasicTopbarComponents';
import TopbarMenuLink, { TopbarLink } from './TopbarMenuLink';
import { getIDFirst } from '../../../../redux/actions/getIDFirstAction';
import { changeDataArrChuHang } from '../../../../redux/actions/arrChuHangAction';

const Ava = `${process.env.PUBLIC_URL}/img/ava.png`;

const TopbarProfile = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const user = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const cookies = new Cookies();
  const history = useHistory();
  const toggleProfile = () => {
    setIsCollapsed(!isCollapsed);
  };

  const logout = () => {
    cookies.remove('token', { path: '/' });
    cookies.remove('hoten', { path: '/' });
    cookies.remove('ma_dang_nhap', { path: '/' });
    cookies.remove('idfirst', { path: '/nandhlogistics/tables' });
    cookies.remove('idfirst', { path: '/' });
    cookies.remove('idfirst', { path: '/nandhlogistics' });
    cookies.remove('tenviettat', { path: '/' });
    cookies.remove('tenchuhang', { path: '/' });
    cookies.remove('accessToken', { path: '/' });
    cookies.remove('checkcontain', { path: '/' });
    localStorage.removeItem('tenviettat');
    localStorage.removeItem('tenchuhang');
    localStorage.removeItem('datefrom');
    localStorage.removeItem('dateto');
    localStorage.removeItem('datefromimport');
    localStorage.removeItem('datetoimport');
    localStorage.removeItem('checkcontain');
    localStorage.removeItem('accessToken');
    localStorage.removeItem('datefromimportmanage');
    localStorage.removeItem('datetoimportmanage');
    localStorage.removeItem('datefromexport1');
    localStorage.removeItem('datetoexport1');
    localStorage.removeItem('datefromexportmanage');
    localStorage.removeItem('datetoexportmanage');
    localStorage.removeItem('datefromimportdetail');
    localStorage.removeItem('datetoimportdetail');
    localStorage.removeItem('datefromexport');
    localStorage.removeItem('datetoexport');
    localStorage.removeItem('datefromhistory');
    localStorage.removeItem('datetohistory');
    localStorage.removeItem('datefromsystemimport');
    localStorage.removeItem('datetosystemimport');
    localStorage.removeItem('datefromsystemexport');
    localStorage.removeItem('datetosystemexport');
    localStorage.removeItem('datefromsystemexport');
    localStorage.removeItem('datetosystemexport');
    localStorage.removeItem('datefromxuatnhapton');
    localStorage.removeItem('datetoxuatnhapton');
    localStorage.removeItem('datefromsln');
    localStorage.removeItem('datetosln');
    localStorage.removeItem('datefromslx');
    localStorage.removeItem('datetoslx');

    // localStorage.clear();
    dispatch(changeDataArrChuHang({ arrChuHang: [] }));
    history.push('/');
  };

  return (
    <TopbarProfileWrap>
      <TopbarAvatarButton
        type="button"
        onClick={toggleProfile}
      >
        <TopbarAvatarImage
          src={(user && user.avatar) || Ava}
          alt="avatar"
        />
        <TopbarAvatarName>
          {cookies.get('hoten') || localStorage.getItem('hoten')}
        </TopbarAvatarName>
        <TopbarDownIcon />
      </TopbarAvatarButton>
      {isCollapsed && (
        <TopbarBack
          type="button"
          aria-label="profile button"
          onClick={toggleProfile}
        />
      )}
      <Collapse in={isCollapsed}>
        <TopbarMenuWrap>
          <TopbarMenu>
            <TopbarMenuLink
              title="Log Out"
              icon="exit"
              onClick={logout}
            />
          </TopbarMenu>
        </TopbarMenuWrap>
      </Collapse>
    </TopbarProfileWrap>
  );
};

export default TopbarProfile;

// region STYLES

export const TopbarProfileWrap = styled.div`
  position: relative;
  margin-bottom: 0;
  ${marginLeft}: 0;

  @media screen and (max-width: 576px) {
    margin: inherit;
    display: none;
  }

  @media screen and (max-width: 600px) {
    font-size: 11px !important;
  }

  @media screen and (max-width: 320px) {
    margin: auto 0;
  }
`;

const TopbarAvatarButton = styled.button`
  height: 100%;
  display: flex;
  cursor: pointer;
  position: relative;
  border-radius: 0;
  border: none;
  transition: all 0.3s;
  box-shadow: none;
  ${'' /* padding: 0 15px; */}
  background-color: transparent;

  &:hover,
  &:focus,
  &:active,
  &:focus:active {
    background-color: ${colorHover};
  }

  &:focus {
    outline: none;
  }

  &:before {
    display: none;
  }
`;

const TopbarAvatarImage = styled.img`
  margin: auto 0;
  border-radius: 50%;
  height: 36px;
  width: 36px;
`;

export const TopbarAvatarName = styled.p`
  margin: auto 0;
  font-size: 13px;
  line-height: 18px;
  font-weight: 400;
  display: none;
  ${marginLeft}: 10px;
  color: ${colorText};

  @media screen and (min-width: 480px) {
    display: block;
  }
  @media screen and (max-width: 600px) {
    font-size: 11px;
  }
`;

const TopbarMenuWrap = styled.div`
  z-index: 101;
  position: absolute;
  width: 100%;
  min-width: 210px;
  ${right}: 0;

  @media screen and (max-width: 320px) {
    ${right}: -50px;
  }
`;

const TopbarMenu = styled.div`
  width: 200px;
  border-radius: 0;
  border: none;
  padding: 15px 0;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.05);
  margin-top: 0;
  background: ${colorBackground};

  button {
    padding: 0;

    &:hover {
      background-color: ${colorHover};
    }

    &${TopbarLink} {
      background-color: transparent;
      border: none;
      padding: 9px 20px;
    }
  }

  *:focus {
    outline: none;
  }

  @media screen and (min-width: 480px) {
    width: 100%;
    ${left}: 0 !important;
  }
`;

const TopbarMenuDivider = styled.div`
  margin: 15px 0;
  border-top: 1px solid ${colorBorder};
`;

// endregion
